.app {
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 16px;
  padding-bottom: 24px;
}
.nav {
  display: flex;
  align-items: center;
  border-bottom: 3px solid #757575;
}
.link {
  display: flex;
  align-items: center;
  padding: 20px 0 20px 20px;

  text-decoration: none;
  color: #212121;
  font-weight: 500;
  font-size: 24px;
  line-height: 1.14;
  letter-spacing: 0.02em;

  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
.link:hover,
.link:focus {
  color: #2196f3;
}

.activeLink {
  color: #2196f3;
}

